@import './../../styles/sass/variables';
@import './../../styles/sass/mixins';

.title {
  margin: 0 0 30px;
  text-transform: capitalize;
  letter-spacing: 5px;
  position: relative;
  text-align: center;

  & h2 {
    margin: 0;
    @include text($titleFontFamily, 40px, 1.2, 500);
  }

  &::before {
    content: "";
    position: absolute;
    background: $primaryColor;
    width: 55px;
    height: 1px;
    bottom: -30px;
    left: 50%;
    transform: translate(-50%, 50%);
  }
}